import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home";
import Login from "./views/Login";
import Signup from "./views/Signup";
import Display from "./views/Display";
import Music from "./views/Music";
import Animation from "./views/Animation";
import Cast from "./views/Cast";
import Room from "./views/Room";
import Agreement from "./views/Agreement";
import CommunityAgreement from "./agreement_pages/CommunityAgreement";
import HostAgreement from "./agreement_pages/HostAgreement"
import UserAgreement from "./agreement_pages/UserAgreement"
import PrivacyAgreement from "./agreement_pages/PrivacyAgreement"
import LicenseAgreement from "./agreement_pages/LicenseAgreement"
import ContactPage from "./agreement_pages/ContactPage"
import Artwork from "./views/Artwork"

Vue.use(Router);

export default new Router({
  routes: [
    { path: "/", name: "home", component: Home },
    { path: "/login", name: "login", component: Login },
    { path: "/signup", name: "signup", component: Signup },
    { path: "/display", name: "display", component: Display },
    { path: "/music", name: "music", component: Music },
    { path: "/animation", name: "animation", component: Animation },
    { path: "/artwork", name: "artwork", component: Artwork },
    { path: "/cast", name: "cast", component: Cast },
    { path: "/room/:id", name: "room", component: Room },
    {
      path: "/agreement",
      name: "agreement",
      component: Agreement,
      children: [
        {
          path: "CommunityAgreement",
          component: CommunityAgreement
        },
        {
          path: "HostAgreement",
          component: HostAgreement
        },
        {
          path: "UserAgreement",
          component: UserAgreement
        },
        {
          path: "PrivacyAgreement",
          component: PrivacyAgreement
        },
        {
          path: "LicenseAgreement",
          component: LicenseAgreement
        },
        {
          path: "ContactPage",
          component: ContactPage
        }
      ]
    }
  ]
});
