<template>
<div class="log-in">
  <div class="form-container">
      <a-form
    id="components-form-demo-normal-login"
    :form="form"
    class="login-form"
    @submit="handleSubmit"
  >
    <a-form-item>
      <a-input
        v-decorator="[
          'userName',
          { rules: [
          { required: true, message: '请输入用户名' }
          ] }
        ]"
        placeholder="用户名"
      >
        <a-icon
          slot="prefix"
          type="user"
          style="color: rgba(0,0,0,.25)"
        />
      </a-input>
    </a-form-item>
    <a-form-item>
      <a-input
        v-decorator="[
          'password',
          { rules: [
          { required: true, message: '请输入密码' }
          ] }
        ]"
        type="password"
        placeholder="密码"
      >
        <a-icon
          slot="prefix"
          type="lock"
          style="color: rgba(0,0,0,.25)"
        />
      </a-input>
    </a-form-item>
    <a-form-item>
      <a-button
        type="primary"
        html-type="submit"
        class="login-form-button"
      >
        登陆
      </a-button>
    
    </a-form-item>
  </a-form>
  </div>

</div>
  
</template>

<script>
export default {
   beforeCreate () {
    this.form = this.$form.createForm(this);
  },

  data(){
    return {
      username: "blinnnk",
      password: "123"
    }
  },

  methods: {
    handleSubmit (e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values)
          if(values.userName === this.username && values.password === this.password) {
            console.log('should emit event')
            this.$emit('loginEvent')
            this.$router.push({path: '/'})
          }
        }
      });
    }
  }
}
</script>

<style>
.log-in {
  padding: 5em 0 0 0;
  height:100%;
  margin-bottom: -80px;
}
.form-container {
  width: 30%;
  transform: translate(120%, 120%);
}

.login-form {
  width:100%;
}

</style>