<template>
  <a-locale-provider :locale="zh_CN">
    <div id="app">
      <Navbar :loggedIn="loggedIn" @logoutEvent="logoutEvent"/>
      <router-view @loginEvent="loginEvent"  class="main"></router-view>
      <Footbar class="footer" />
    </div>
  </a-locale-provider>
</template>

<script>
import zh_CN from 'ant-design-vue/lib/locale-provider/zh_CN'
import Navbar from './components/Navbar'
import Footbar from './components/Footbar'

export default {
  name: 'app',
  data() {
    return {
      zh_CN,
      loggedIn: false
    }
  },
  components: {
    Navbar,
    Footbar
  },
  methods: {
    loginEvent() {
      localStorage.loggedIn = true;
      this.loggedIn = true;
    },

    logoutEvent() {
      localStorage.loggedIn = false;
      this.loggedIn = false;
    }
  },
  mounted() {
    console.log(localStorage.loggedIn)
    if (localStorage.loggedIn == true) {
      this.loggedIn = true;
    } else {
      this.loggedIn = false;
    }
  }
}
</script>

